.clearfix:after {
  content: "";
  display: block;
  clear: both;
}
::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 7px;
  height: 8px;
}
::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.5);
  box-shadow: 0 0 1px rgba(255, 255, 255, 0.5);
}
.app-root,
.global-layout {
  height: 100%;
}

.search-info {
  color: rgba(255, 255, 255, 0.62);
  padding-top: 4px;
  font-size: 12px;
}
.ant-spin-dot-item {
  background-color: #ffffff;
}
